@import url('https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400&display=swap');

* {
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Inter', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: 'Lora', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-size: calc(25px + 0.390625vw);
  font-weight: 300;
}

h2 {
  font-family: 'Lora', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-size: calc(18px + 0.390625vw);
  font-weight: 300;
}

section {
  box-shadow: inset 0px 5px 5px -5px rgba(0, 0, 0, 0.1);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

@keyframes rotate {

  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }

}

.out-top {
  animation: rotate 20s linear infinite;
  transform-origin: 13px 25px;
}

.in-top {
  animation: rotate 10s linear infinite;
  transform-origin: 13px 25px;
}

.out-bottom {
  animation: rotate 25s linear infinite;
  transform-origin: 84px 93px;
}

.in-bottom {
  animation: rotate 15s linear infinite;
  transform-origin: 84px 93px;
}

.container {
  
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  overflow-x: hidden;

}

.content {

  font-size: calc(15px + 0.390625vw);
  margin-left: 5vw; 
  margin-right: 5vw;

  @media screen and (min-width: 769px) {
    font-size: calc(10px + 0.390625vw);
    margin-left: 20vw; 
    margin-right: 20vw;
  }

}

.column-container {
  
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 769px) {
    flex-direction: row;
  }

}

.column {

  width: 100%;
  margin-right: 0%;

  @media screen and (min-width: 769px) {
    width: 45%;
    margin-right: 10%;
  }

}

.badge {
  margin-right: 5px;
}

.title-container {

  position: absolute;
  
  top: max(min(50%, calc(80vh - 70vw)), 20vh); 
  left: 50%; 
  transform: translate(-50%, -50%);

  @media screen and (min-width: 768px) {
    top: 40%;
    left: 20%; 
    transform: translate(0%, -40%);
  }

}

.title {

  color: white; 
  font-size: 2rem; 
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);

  padding: 10px;
  border-radius: 10px;
  border: 1px solid #253035;
  background-color: #101517;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);

  @media screen and (min-width: 375px) {
    font-size: 2.5rem; 
  }

  @media screen and (min-width: 769px) {
    font-size: 3.5rem; 
  }

}

.landing-portrait {

  position: absolute;
  top: 100vh;
  z-index: 10;

  right: 50vw;
  transform: translate(50%, -100%);
  width: min(70vw, 60vh);
  
  @media screen and (min-width: 768px) {
    right: 10vw;
    transform: translate(0%, -100%);
    width: min(40vw, 60vh);
  }

}